.sug-loc {
  padding-top: 120px;
  padding-bottom: 120px;

  background-color: $secondary-background-color;
}
.sug-loc__wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
}
.sug-loc__description {
  width: 530px;
}
.sug-loc__subtitle {
  margin-bottom: 10px;

  line-height: 1.13;

  color: $secondary-accent-color;
}
.sug-loc__title {
  margin-bottom: 20px;

  font-family: $secondary-font-title;
  font-size: 42px;
  line-height: 1.24;

  color: $header-color;
}
.sug-loc__buttons {
  align-self: flex-end;
}
.sug-loc__btn {
  max-width: 50px;
  min-width: 0;
  height: 50px;
}
